import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
const state = {
  stepName: ['', '', ''],
  steps: [1, 2, 3, 4, 5, 6],
  step: 1,
  description: ['', ',', '', '', '', ''],
  radioStep1: '',
  form: {
    company: '',
    name: '',
    state: '',
    sizeAndCapacitys: '',
    categorys: [],
    unit: '',
    type: [],
    voltagelevel1: '',
    voltagelevel2: 100,
    voltagelevel3: '',
    place: '',
    major: [],
    designState: '',
    startTime: '',
    endTime: '',
    character: '',
    performanceReq1: '',
    performanceReq2: '',
    lowPrice: '',
    highPrice: '',
    priceType: '',
    aptitude: '',
    paymentMethods: '',
    isAcceptJointBid: 'true',
    stateUnits: [
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' },
      { state: '', endTime: '', requireResult: '' }
    ],
  },
  qualificationRequirements: { CET: '' },
  projectCharacteristics: { difficulty: '', price: '' },
  unit: ['MW', 'Kva', 'KV', `M^2`],
  existingData: { detail: '' },
  states: [{ 'value': '', 'label': '' }, { 'value': '', 'label': '' }],
  major: ['', '', '', ''],
  designState: ['', '', '', '', '', '', ''],
  sizeAndCapacitys: ['MW', 'KW', 'kVA'],
  categorys: ['', '', '', '', '', ''],
  type: ['', '', '', '', ''],
  voltagelevel: ['KV', 'V'],
  voltagetype: ['', ''],
  character: ['', ''],
  performanceReq: ['', ''],
  priceType: ['', ''],
  aptitude: ['', ''],
  paymentMethods: ['', '']
}

const mutations = {
  init(state) {
    state.step = 1
  },
  init2(state) {
    state.step = 2
    axios.post('http://39.106.34.156:8080/electric-design/getTypeMajors').then((res) => {
      state.categorys = res.data.categorys
      state.type = res.data.types
      state.designState = res.data.designProcess
      state.sizeAndCapacitys = res.data.sizeAndCapacitys
      state.major = res.data.majors
    }).catch((err) => {
      console.log(err)
      this.$message({
        showClose: true,
        message: '',
        type: 'error'
      })
    })
  },
  init3(state) {
    state.step = 3
  },
  init4(state) {
    state.step = 4
  },
  init5(state) {
    state.step = 5
  },
  init6(state) {
    state.step = 6
  },
  addStateUnits(state) {
    state.form.stateUnits.push({ state: '', endTime: '', requireResult: '' })
  },
  myDelete(state, el) {
    state.form.stateUnits = state.form.stateUnits.filter(o => o.state !== el.state)
  }
}
export default new Vuex.Store({
  state,
  mutations
})



