<template>
  <div class="PM-new-step2">
    <div class="title"><span></span></div>
    <div class="content">
      <el-form ref="form" :model="form" label-width="80px" style="width:100%"> 
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.company"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.name"></el-input>
          </el-col>
          <el-col class="line" :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.categorys" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in categorys" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="4">
            <el-select v-model="form.state"  placeholder="" >
            <el-option v-for="(i,j) in states" :key="j" :label="i.label" :value="i.value"></el-option>
          </el-select>
          </el-col>
          <el-col class="line" :span="1"></el-col>
          <el-col :span="5">
            <el-input v-model="form.sizeAndCapacitys" placeholder="" type="number"></el-input>
          </el-col>
          <el-col :span="3">
            <el-select v-model="form.unit"  placeholder="" >
            <el-option v-for="(i,j) in unit" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
          <el-col :offset="1" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.designState" style="width:100%">
              <el-option v-for="(i, j) in designState" :key="j"  :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.type" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in type" :key="j" :label="i" :value="i"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-select v-model="form.major" multiple placeholder="" style="width:100%" filterable allow-create default-first-option>
            <el-option v-for="(i,j) in major" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.voltagelevel1" style="width:30%">
              <el-option v-for="(i,j) in voltagetype" :key="j" :label="i" :value="i"></el-option>
            </el-select>
            <el-input v-model="form.voltagelevel2" style="width:35%" type="number"></el-input>
            <el-select v-model="form.voltagelevel3" placeholder="" style="width:30%">
              <el-option v-for="(i,j) in voltagelevel" :key="j" :label="i" :value="i"></el-option>
            </el-select>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-date-picker v-model="form.startTime" type="date" placeholder=""></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-input v-model="form.place"></el-input>
          </el-col>
          <el-col :offset="5" :span="2"></el-col>
          <el-col :span="8">
            <el-date-picker v-model="form.endTime" type="date" placeholder=""></el-date-picker>
          </el-col>
        </el-form-item>
      </el-form>
    </div>
    <div class="title"><span></span></div>
    <div class="content">
      <el-form :model="form" label-width="100px" style="width:100%">
        <el-form-item label="">
          <el-col :span="9">
          <el-select v-model="form.character" placeholder="" style="width:100%">
            <el-option v-for="(i,j) in character" :key="j" :value="i" :label="i"></el-option>
          </el-select>
          </el-col>
          <el-col :offset="2" :span="3"></el-col>
          <el-col :span="4">
            <el-select v-model="form.performanceReq1" placeholder="" style="width:100%"  filterable allow-create default-first-option>
              <el-option v-for="(i,j) in performanceReq" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
          <el-col :span="3"></el-col>
          <el-col :span="3">
            <el-input v-model="form.performanceReq2" style="width:100%"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
          <el-input v-model="form.lowPrice" placeholder="" style="width:40%;"></el-input>
          <span style="font-size:1.2rem;color:red"></span>
          <el-input v-model="form.highPrice" placeholder="" style="width:40%;"></el-input>
          </el-col>
          <el-col :offset="2" :span="5"></el-col>
          <el-col :span="8">
            <el-select v-model="form.aptitude" placeholder="" style="width:100%">
              <el-option v-for="(i,j) in aptitude" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="9">
            <el-select v-model="form.paymentMethods" placeholder="" style="width:100%">
              <el-option v-for="(i,j) in paymentMethods" :key="j" :value="i" :label="i"></el-option>
            </el-select>
          </el-col>
          <el-col :offset="2" :span="5"></el-col>
          <el-col :span="8">
           <el-switch v-model="form.isAcceptJointBid" active-text="" inactive-text="" inactive-value="false" active-value="true"></el-switch>
          </el-col>
        </el-form-item>
      </el-form>
    </div>
    <div class="title"></div>
    <div class="content">
      <el-form :model="form" label-width="80px" style="width:100%">
        <el-radio v-model="radio" label="1"></el-radio>
        <el-radio v-model="radio" label="2"></el-radio>
        <el-table :data="form.stateUnits" stripe style="margin-top:2rem">
          <el-table-column label="">
            <template slot-scope="scope">
              <el-select v-model="scope.row.state">
                <el-option v-for="(i, j) in designState" :key="j" :label="i" :value="i"></el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-date-picker v-model="scope.row.endTime" :disabled="disabled" type="date" placeholder=""></el-date-picker>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-input type="textarea" autosize :disabled="disabled" v-model="scope.row.requireResult"></el-input>  
            </template>            
          </el-table-column>
          <el-table-column label="">
             <template slot-scope="scope">
               <el-button type="danger" @click="myDelete(scope.row)"><i class="icon iconfont icon-cha"></i>{{scope.row.state}}</el-button>
             </template>
          </el-table-column>
        </el-table>
        <el-button type="primary" @click="addStateUnits()"></el-button>
      </el-form>
    </div>
    <div class="skip">
      <div style="margin-left:2rem" @click="$router.push('/per/PM-new')"><i class="icon iconfont icon-zuo"></i><span></span></div>
      <div style="margin-right:2rem" @click="$router.push('/per/PM-new/3')"><span></span><i class="icon iconfont icon-you"></i></div>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/step.js'
import {mapState, mapMutations} from 'vuex'
export default {
  store,
  computed: {
    ...mapState(['step', 'form', 'states', 'designState', 'type', 'categorys', 'sizeAndCapacitys', 'voltagelevel', 'voltagetype', 'major', 'unit', 'character', 'performanceReq', 'priceType', 'aptitude', 'paymentMethods'])
  },
  data () {
    return {
      disabled: false,
      radio: '2'
    }
  },
  methods: {
    ...mapMutations(['init2', 'addStateUnits', 'myDelete'])
  },
  mounted () {
    this.init2()
  },
  watch: {
    radio () {
      if (this.radio === '1') {
        this.disabled = true
      } else {
        this.disabled = false
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.skip{
  margin-top:1rem;
  display:flex;
  justify-content:space-between;
  align-items:center;
  color:#4d83e7;
  font-weight:500;
}
.title{
  display:flex;
  justify-content:flex-start;
  width:80%;
  margin:3rem auto 0 auto;
  font-weight:400;
  font-size:1.5rem;
  border-bottom: 1px solid black;
}
.content{
  margin:3rem auto 0 auto;
  width:80%;
  display:flex;
}
</style>



