<template>
  <div>
    <div class="content">
      <p style="text-align:left;width:100%"></p>
      <div style="margin:1rem auto">
        <el-button type="primary" @click="dialogVisible=true"></el-button>
        <el-button type="primary" @click="dialogVisible=true"></el-button>
      </div>
      <el-dialog title="" :visible.sync="dialogVisible" width="30%">
        <span>ckjxxx</span>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
      <p style="text-align:left;width:100%"></p>
      <div style="margin:1rem auto">
        <el-button type="primary" @click="$router.push('/com/CM-new/2')"></el-button>
      </div>
    </div>
    <div class="skip">
      <div style="margin-left:2rem" @click="$router.push('/com/CM-new/1')"><i class="icon iconfont icon-zuo"></i><span></span></div>
      <div style="margin-right:2rem" @click="$router.push('/com/CM-new/3')"><span></span><i class="icon iconfont icon-you"></i></div>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/step.js'
import {mapState, mapMutations} from 'vuex'
export default {
  store,
  computed: {
    ...mapState(['step', 'form'])
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    ...mapMutations(['init2'])
  },
  mounted () {
    this.init2()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.skip{
  margin-top:4rem;
  display:flex;
  justify-content:space-between;
  align-items:center;
  color:#4d83e7;
  font-weight:500;
}
.content{
  margin:3rem auto 0 auto;
  width:80%;
}
</style>



